import { graphql, PageProps } from "gatsby";
import { MDXRenderer } from "gatsby-plugin-mdx";
import React, { FC } from "react";
import { Helmet } from "react-helmet";
import Layout from "../components/common/Layout/Layout";
import {
  PolicyContent,
  PolicyContentContainer,
} from "../components/policies/styled";

const PolicyPage = ({ data }: PageProps<Queries.PolicyPageQuery>) => {
  return (
    <>
      <Helmet
        title={`Telgorithm | ${data.mdx?.frontmatter?.title}`}
      />
      <Layout>
        <PolicyContentContainer>
          <PolicyContent>
            <MDXRenderer children={data?.mdx?.body ?? "No content"} />
          </PolicyContent>
        </PolicyContentContainer>
      </Layout>
    </>
  );
};

export default PolicyPage;

export const query = graphql`
  query PolicyPage($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        title
      }
      body
    }
  }
`;
